.header {
  display: block;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;

  max-width: 100%;
  height: 80px;
}

.nav {
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav__logo {
  display: block;

  margin-right: 92px;

  color: $accentColor;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: $fontSecond;
  font-size: 26px;
  font-weight: 500;
  line-height: 1.14;
  letter-spacing: 0.02em;

  cursor: pointer;
  @media screen and (max-width: $tabletScreen) {
     margin-right: 42px;
  }
}
.span__logo {
 color: $blackColor;
}

.nav__list{
  display: flex;
  justify-content: center;
  align-items: center;
  @media screen and (max-width: $tabletScreen) {
    // margin-right: 60px;
  }
}

.contacts__items {
  display: flex;
  justify-content: center;
  align-items: center;

  gap: 53px;
}

.contact__item {
  position: relative;
}

.contact__item--link {
  color: $blackColor;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
  letter-spacing: 0.02em;

  cursor: pointer;
  
  &:hover, &:focus {
    color: $accentColor;
  }
  &:active{
    color: $accentColor;
  }
  &::after {
  position: absolute;
  content: "";
  width: 0;
  height: 4px;
  border-radius: 0 2px 2px 0;
  bottom: -30px;
  left: 49.5%;
  transition: 0.5s;
  background-color: $accentColor;
  margin: -1px;
  padding: 0;
}

&::before {
  position: absolute;
  content: "";
  width: 0;
  height: 4px;
  border-radius: 2px 0 0 2px;
  bottom: -30px;
  left: 50.5%;
  transition: 0.5s;
  background-color: $accentColor;
  transform: translatex(-100%);
  margin: -1px;
  padding: 0;
}


&:hover::after {
  width: 50%;
}

&:hover::before {
  width: 50%;
}

}

.links__items {
  display: flex;
  justify-content: center;
  align-items: center;
  
 
  @media screen and (max-width: $tabletScreen) {
    flex-direction: column;
    align-items: flex-start;
  }
}
.link__mail, .link__phone{
  fill: currentColor;
  display: block;
}
.links__item{
  display: flex;
  justify-content: center;
  align-items: center;
  
  &:not(:last-child) {
    margin-right: 30px;
  }

  
  @media screen and (max-width: $tabletScreen) {
    margin-right: 0 auto;
    &:not(:last-child) {
    margin-bottom: 10px;
  }
  }
}

.links__item--link:not(:last-child){
  margin-right: 30px;
}
.links__item--link {
  color: $mainColor;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
  letter-spacing: 0.02em;

  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;

  cursor: pointer;

  &:hover, &:focus {
    color: $accentColor;
    fill: $accentColor;
  }
  &:active{
    color: $accentColor;
    fill: $accentColor;
  }
  
}